/* ---- reset ---- */

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  background-color: #391552;
}

// canvas {
//   display: block;
// }
.container {
  position: relative;
  display: flex;
  flex : 1;
  flex-direction: column;
  align-items: stretch;
  min-height: 100vh;
}

.top-content {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex : 4;
}

.main-content {
  display: flex;
  flex : 100;
  flex-direction: row;
  align-items: stretch;
}

